"use strict";

const cardsRestaraunts = document.querySelectorAll(".card");

$(document).ready(() => {
  sessionStorage.clear();
});

const getInfo = (item) => {
  let infoArr = [];
  for (let n of item.children)
    for (let item of n.children)
      (item.classList.contains("heading") ||
        item.classList.contains("info") ||
        item.classList.contains("rating")) &&
        infoArr.push(item.textContent);
  return {
    name: infoArr[0],
    rating: infoArr[1],
    startPrice: infoArr[2],
    text: infoArr[3],
  };
};

for (let item of cardsRestaraunts)
  item.addEventListener("click", function () {
    const item = getInfo(this.lastElementChild);
    sessionStorage.setItem(item.name, JSON.stringify(item));
  });

new WOW().init();
